import React, { Component } from 'react';
import { Button, Card, Popover, Result, Spin, Modal, Progress, Table, Divider } from 'antd';
import { Link } from 'react-router-dom';
import '../../App.css';
import airdrop from '../../ABI/LGairdrop';
import web3 from "../../ABI/web3";
import LG from '../../ABI/LG';
import airdropEveryDay from '../../ABI/airdropEveryDay';
import airdropTotal from '../../ABI/airdropTotal';
import {
  SmileOutlined, FrownTwoTone, MehTwoTone, LikeTwoTone,
  QuestionCircleOutlined, CopyOutlined
} from '@ant-design/icons';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import Invite from '../../ABI/invite';
import LGC from '../../ABI/LGC';
import '../../components/global';
import LGCwindow from '../../components/LGCwindow';
import moment from 'moment';
const BigNumber = require('bignumber.js');

class lgairdrop extends Component {
  constructor(props) {
    super(props);
    this.state = {
      AirdropAmount: "0",
      airdropadr: global.constants.LGairdrop,  //LG空投合约地址
      AirdropAmountEveryDay: "0",
      time: "",
      message: "",
      _adayamount: '', //日额余额
      loadwindow: false, //加载窗
      loading: 'block', //加载中
      complete: 'none', //加载完成
      loadmessage: '',  //加载窗信息
      Resultwindow: '', //结果窗
      lastHB: '',  //每日一领处理后结果
      _lastHB: '', //每日一领原始结果
      chainIdDisabled: '',
      time_hour: '',
      time_minute: '',
      time_second: '',
      _icon: '',
      invite: '0x0000000000000000000000000000000000000001', //推荐人
      inviteMy: '', //邀请链接
      friendWindow: false,
      friendList: {},
      friendInfoWindow: '',
      friendInfo: '',
      usertotal: '',
      disabledUserGet: 'disabled', //用户领取按鈕开关
      disabledGongYin: 'disabled', //供应按鈕开关
      buttonUserGet: '领取',
      buttonGongYin: '供应',
      AirdropEveryDayAmount: '',
      ChainID: ''
    }
  }

  async componentDidMount() {
    if (window.ChainID !== '0x80') {
      this.setState({
        chainIdDisabled: "disabled",
        disabledUserGet: "disabled",
        disabledGongYin: "disabled",
        message: "请切换至火币链领取空投"
      })
    } else {
      this.setState({
        chainIdDisabled: "",
        disabledUserGet: "",
        disabledGongYin: "",
        message: ""
      })
    }

    //邀请链接处理
    const invite = {};
    const _invite = window.location.search;
    const reg = /[?&][^?&]+=[^?&]+/g;
    const found = _invite.match(reg);
    if (found) {
      found.forEach(item => {
        let temp = item.substring(1).split('=');
        let key = temp[0];
        let value = temp[1];
        invite[key] = value;
      })
    }
    if (invite.address !== undefined) {
      this.setState({ invite: invite.address })
    }
    const url = window.location.host;
    this.setState({ inviteMy: 'https://' + url + '/?address=' + window.defaultAccount })
    // this.setState({inviteMy:'http://localhost:3000/?address='+ window.defaultAccount})

    //获取空投池LG余额
    const Airdrop_LGbalance = await LG.methods.balanceOf(this.state.airdropadr).call();
    this.setState({ Airdrop_LGbalance });

    //获取每日空投池LG余额
    const AirdropEveryDayAmount = await LG.methods.balanceOf(global.constants.airdropEveryDay).call();
    this.setState({ AirdropEveryDayAmount: parseFloat(web3.utils.fromWei(AirdropEveryDayAmount, "ether")).toFixed(0) });

    //获取用户累额
    const usertotal = await airdropTotal.methods.getUsertotal(window.defaultAccount).call();
    const cc = new BigNumber(web3.utils.fromWei(usertotal, "ether")).toFixed();
    this.setState({ usertotal: cc });
    window.usertotal = cc

    //获取首页LG空投单个量
    const AirdropAmount = await airdrop.methods.AirdropAmount().call();
    if (AirdropAmount / 10 ** 18 <= Airdrop_LGbalance / 10 ** 18) {
      this.setState({ AirdropAmount: parseFloat(web3.utils.fromWei(AirdropAmount, "ether")).toFixed(2) })
    }

    //供应倒计时
    const time0 = await airdropEveryDay.methods.getTime().call();
    const date2 = moment().add({ seconds: time0 }).format("HH:mm")
    const date3 = moment().add({ seconds: time0 }).format("D")
    if (time0 > 200) {
      this.setState({
        disabledGongYin: 'disabled',
        buttonGongYin: date3 + '号' + date2 + '供应'
      })
    }
    if (time0 <= 200) {
      setInterval(() => {
        this.time0()
      }, 1000);
      this.setState({
        disabledGongYin: 'disabled',
        buttonGongYin: time0
      })
    }
    if (time0 === '0') {
      this.setState({
        disabledGongYin: '',
        buttonGongYin: '供应'
      })
    }

    //用户领取时间倒计时
    const timeuesr = await airdropEveryDay.methods.userTime().call({ from: window.defaultAccount });
    if (timeuesr > 0) {
      setInterval(() => {
        this.timeuesr()
      }, 1000);
    }
    if (timeuesr !== '0') {
      this.setState({
        disabledUserGet: 'disabled',
        buttonUserGet: timeuesr
      })
    } else {
      this.setState({
        disabledUserGet: '',
        buttonUserGet: '领取'
      })
    }

    //获取每日一领数量
    const AirdropAmountEveryDay = await airdropEveryDay.methods.AirdropAmount().call();
    this.setState({ AirdropAmountEveryDay });

    //获取当日额
    const _adayamount = await airdropEveryDay.methods._adayamount().call();
    const _adayamount2 = parseFloat(web3.utils.fromWei(_adayamount, "ether")).toFixed(0);
    if (_adayamount2 > AirdropAmountEveryDay / 10 ** 18 * 2 * 0.9) {
      this.setState({ _adayamount: _adayamount2 });
    } else {
      this.setState({ _adayamount: 0 });
      this.setState({ disabledUserGet: "disabled" });
      this.setState({ message: "当日额度已领完请等待供应" });
    }

    //获取领取频率
    const time = await airdropEveryDay.methods.time().call();
    this.setState({ time });

  }

  //供应倒计时
  time0 = () => {
    if (this.state.buttonGongYin > 0) {
      var x = this.state.buttonGongYin;
      x = x - 1;
      if (x > 0) {
        this.setState({ buttonGongYin: x })
      }
      if (x === 0) {
        this.setState({
          disabledGongYin: '',
          buttonGongYin: '供应'
        })
      }
    }
  }

  //用户领取倒计时
  timeuesr = () => {
    if (this.state.buttonUserGet > 0) {
      var x = this.state.buttonUserGet;
      x = x - 1;
      if (x > 0) {
        this.setState({ buttonUserGet: x })
      }
      if (x === 0) {
        this.setState({
          disabledUserGet: '',
          buttonUserGet: '领取'
        })
      }
    }
  }

  addDayamount = async () => {
    if (this.state._adayamount !== 0) {
      this.setState({ message: '当日额度未领完' })
    } else {
      await airdropEveryDay.methods.addDayamount().send({ from: window.defaultAccount })
        .on('transactionHash', (transactionHash) => {
          if (transactionHash.length !== 0) {
            this.setState({
              loadmessage: "打包中……",
              loadwindow: true,
              loading: 'block',
              complete: 'none'
            })
          }
        })
        .on('receipt', (receipt) => {
          if (receipt.status === true) {
            this.setState({
              loadmessage: "供应成功，可继续领取",
              loading: 'none',
              complete: 'block'
            })
          }
        })
    }
  }

  getResult = async () => {
    const lastHB = this.state._lastHB;
    const _lastHB = await parseFloat(web3.utils.fromWei(lastHB, "ether")).toFixed(2);
    const bb = await parseFloat(web3.utils.fromWei(this.state.AirdropAmountEveryDay, "ether")).toFixed(2);
    this.setState({ lastHB: _lastHB + 'LG' });
    const iconL = (
      <FrownTwoTone />
    );
    const iconLM = (
      <MehTwoTone />
    );
    const iconGM = (
      <SmileOutlined />
    );
    const iconG = (
      <LikeTwoTone />
    );
    if (_lastHB < (bb * 2 * 0.15)) {
      this.setState({ _icon: iconL })
    }
    if (_lastHB > (bb * 2 * 0.15) && _lastHB < (bb * 2 * 0.5)) {
      this.setState({ _icon: iconLM })
    }
    if (_lastHB >= (bb * 2 * 0.5) && _lastHB < (bb * 2 * 0.85)) {
      this.setState({ _icon: iconGM })
    }
    if (_lastHB > (bb * 2 * 0.85)) {
      this.setState({ _icon: iconG })
    }
  }

  _getAirdrop = async () => {
    if (Number(window.userLGC) < 10) {
      this.LGCwindow.Open("需申请信用分'LGC'才可领取")
    } else {
      await airdrop.methods.getAirdrop(this.state.invite).send({ from: window.defaultAccount })
        .on('transactionHash', (transactionHash) => {
          if (transactionHash.length !== 0) {
            this.setState({
              loadmessage: "打包中……",
              loadwindow: true,
              loading: 'block',
              complete: 'none'
            })
          }
        })
        .on('receipt', (receipt) => {
          if (receipt.status === true) {
            this.setState({
              loadmessage: "领取成功可继续领取下面的1天1领",
              loading: 'none',
              complete: 'block'
            })
          }
        })
    }
  }

  getAirdrop = async () => {
    const _LGbalance = await LG.methods.balanceOf(this.state.invite).call();
    const LGbalance = parseFloat(web3.utils.fromWei(_LGbalance, "ether")).toFixed(2);
    if (this.state.invite.toLowerCase() !== '0x0000000000000000000000000000000000000001') {
      if (LGbalance <= 8) {
        this.setState({ message: '该邀请人地址内LG少于8' })
      }
    }
    if (this.state.invite.toLowerCase() === window.defaultAccount.toLowerCase()) {
      this.setState({ message: '不可以自己用自己的邀请链接进入' })
    }
    if (this.state.invite.toLowerCase() !== window.defaultAccount.toLowerCase() && LGbalance >= 8) {
      this._getAirdrop();
    }
    if (this.state.invite.toLowerCase() === '0x0000000000000000000000000000000000000001') {
      this._getAirdrop();
    }
  }

  _getAirdropEveryDay = async () => {
    await airdropEveryDay.methods.getAirdrop(this.state.invite).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.setState({
            loadmessage: "打包中……",
            loadwindow: true,
            loading: 'block',
            complete: 'none'
          })
        }
      })
      .on('receipt', (receipt) => {
        console.log(receipt)
        console.log(receipt.status)
        console.log(receipt.events[0].raw.data)
        this.setState({ _lastHB: receipt.events[0].raw.data })
        if (receipt.status === true) {
          this.setState({ loadwindow: false })
        }
      })
    this.ResultwindowOpen();
  }

  getAirdropEveryDay = async () => {
    if (this.state.AirdropEveryDayAmount < 30) {
      this.setState({ message: '本轮空投已结束，请等待下一轮空投' })
    } else {
      if (window.userLGC < 10) {
        this.LGCwindow.Open("需申请信用分'LGC'才可领取")
      } else {
        const _LGbalance2 = await LG.methods.balanceOf(window.defaultAccount).call();
        const LGbalance2 = new BigNumber(web3.utils.fromWei(_LGbalance2, "ether")).toFixed();
        if (Number(LGbalance2) < Number(this.state.usertotal)) {
          this.setState({ message: '您帐户中的LG数量小于累额。您可能卖出了您之前领取的LG，请前往LG交易所买回即可继续领取。' })
        } else {
          const _LGbalance = await LG.methods.balanceOf(this.state.invite).call();
          const LGbalance = parseFloat(web3.utils.fromWei(_LGbalance, "ether")).toFixed(2);
          if (this.state.invite.toLowerCase() !== '0x0000000000000000000000000000000000000001') {
            if (LGbalance <= 1) {
              this.setState({ message: '该邀请人地址内LG少于1' })
            }
          }
          if (this.state.invite.toLowerCase() === window.defaultAccount.toLowerCase()) {
            this.setState({ message: '不可以自己用自己的邀请链接进入' })
          }
          if (this.state.invite.toLowerCase() !== window.defaultAccount.toLowerCase() && LGbalance > 1) {
            this._getAirdropEveryDay();
          }
          if (this.state.invite.toLowerCase() === '0x0000000000000000000000000000000000000001') {
            this._getAirdropEveryDay();
          }
          this.componentDidMount();
        }
      }
    }
  }

  friendInfoOpen = async () => {
    const friendList = []
    const friends = await Invite.methods.getFriends().call({ from: window.defaultAccount });
    for (var i = 0; i < friends.length; i++) {
      const lgc = await LGC.methods.balanceOf(friends[i]).call();
      const _lg = await LG.methods.balanceOf(friends[i]).call();
      const lg = parseFloat(web3.utils.fromWei(_lg, "ether")).toFixed(0);
      const a = [];
      a[0] = friends[i];
      a[1] = lgc;
      a[2] = lg;
      friendList.push(a);
    }
    this.setState({ friendList })
    this.setState({ friendWindow: true })
  }

  //加载窗开关
  loadwindowClose = () => {
    this.setState({ loadwindow: false })
  };

  //结果窗关
  ResultwindowClose = () => {
    this.setState({ Resultwindow: false })
  };

  //结果窗开
  ResultwindowOpen = () => {
    this.getResult();
    this.setState({ Resultwindow: true })
  };

  //好友列表窗关
  friendWindowClose = () => {
    this.setState({ friendWindow: false })
  };
  //好友详情窗关
  friendInfoClose = () => {
    this.setState({ friendInfoWindow: false })
  };

  render() {
    const columns = [
      {
        title: '好友帐号',
        dataIndex: '0',
        align: 'left',
        ellipsis: 'true',
      },
      {
        title: 'LGC',
        dataIndex: '1',
        width: '60px',
        align: 'right',
      },
      {
        title: 'LG',
        dataIndex: '2',
        width: '60px',
        align: 'right',
      }
    ];

    const content = (
      <div style={{ color: 'red' }}>
        <b>链改网空投每天领，今日领到</b>
      </div>
    );
    const content1 = (
      <div >
        <p>奖励以好友领取值为基数的75%</p>
        <p>每日一领的奖励百分比将会依市场情况适时调整</p>
        <p>已经绑定完推荐关系后就可以用不带参数的域名，即(lg-qkl.xyz或其它节点域名)</p>
        <p>请保持您帐户里不少于8LG,否则推荐链接无效</p>
        <p>如果您的LG余额少于您伙伴的LG余额，您将无法获得该伙伴领空投时的奖励。</p>
      </div>
    );
   
    return (
      <div>
        <Card type="inner" size='small' title="LG通证空投(LGC&ge;10)" extra={<Link to="/warning">More</Link>}
          style={{ borderRadius: '10px' }}
          headStyle={{ borderTopLeftRadius: '10px', borderTopRightRadius: '10px' }}
        >
          <div>新人福利：{this.state.AirdropAmount}
            <span style={{ float: 'right' }}>
              <Button onClick={() => { this.getAirdrop() }}
                disabled={this.state.chainIdDisabled}
                type="primary"
                size='small'>
                领取
              </Button>
            </span>
          </div>
          <Divider style={{ margin: '5px' }} />
          {/* <div>
            <div >
              {this.state.time}天1领：
              <span>
                <Popover content={content2} trigger="click">
                  <QuestionCircleOutlined />
                </Popover>
                累额：{Math.ceil(this.state.usertotal * 100) / 100}</span>
            </div>
            <div>
              <span>日额{this.state._adayamount}</span>
              <Button
                onClick={() => { this.addDayamount() }}
                disabled={this.state.disabledGongYin}
                type="primary"
                size='small'>
                {this.state.buttonGongYin}
              </Button>
              max{(web3.utils.fromWei(this.state.AirdropAmountEveryDay, "ether") * 2 * 0.9).toFixed(2)}&nbsp;

              <span style={{ float: 'right' }}>
                <Button onClick={() => { this.getAirdropEveryDay() }}
                  disabled={this.state.disabledUserGet}
                  type="primary"
                  size='small'
                >
                  {this.state.buttonUserGet}
                </Button>
              </span>
            </div>
            <div style={{ color: "green", fontSize: "12px", textAlign: 'right' }}>
              {this.state.message}
            </div>
            <Divider style={{ margin: '5px' }} />
          </div> */}
          <div style={{ marginTop: '8px' }}>兑换映射通证
            <span style={{ float: 'right' }}>
              <Link to='PlayToken'>
                <Button type="primary" size='small'>
                  兑换
                </Button>
              </Link>
            </span>
          </div>
          <Divider style={{ margin: '5px' }} />
          <div style={{ marginTop: '5px' }} >
            <Popover content={content1} trigger="click">
              <QuestionCircleOutlined />
            </Popover>
            邀请好友免费领空投得奖励(每天领每天奖)：
            <div >
              <div style={{ width: '18%', float: 'left', marginRight: '2%' }}>
                <Button type='primary' size="small"
                  block
                  onClick={this.friendInfoOpen}
                >好友</Button>
              </div>
              <div style={{ width: '80%', float: 'right' }}>
                <CopyToClipboard text={this.state.inviteMy}>
                  <Button type="primary" size="small"
                    block
                  >复制我的邀请链接</Button>
                </CopyToClipboard>
              </div>
              <div style={{ clear: 'both' }}></div>
            </div>
          </div>

          {/* 加载窗  */}
          <Modal visible={this.state.loadwindow}
            onCancel={this.loadwindowClose}
            footer={[
              <div style={{ textAlign: 'center' }} >
                <Button type='primary' onClick={this.loadwindowClose}>关闭</Button>
              </div>,
            ]}
          >
            <div style={{ textAlign: 'center' }}>
              <div style={{ display: this.state.loading }}>
                <Spin size="large" />
                <div style={{ color: "#1890ff", fontSize: "13px", textAlign: 'center' }}>
                  {this.state.loadmessage}
                </div>
              </div>
              <div style={{ display: this.state.complete }}>
                <Progress type="circle" percent={100} width={45} />
                <div style={{ color: "green", fontSize: "13px", textAlign: 'center' }}>
                  {this.state.loadmessage}
                </div>
              </div>
            </div>
          </Modal>
          {/* 结果窗  */}
          <Modal title={content} visible={this.state.Resultwindow}
            onCancel={this.ResultwindowClose}
            footer={[
              <div style={{ textAlign: 'center' }} >
                <Button type='primary' onClick={this.ResultwindowClose}>关闭</Button>
              </div>,
            ]}
          >
            <span>
              <Result style={{ padding: '30px' }}
                icon={this.state._icon}
                title={this.state.lastHB}
              />
            </span>
          </Modal>
          {/* 好友列表  */}
          <Modal title='' visible={this.state.friendWindow}
            onCancel={this.friendWindowClose}
            footer={[
              <div style={{ textAlign: 'center' }} >
                <Button type='primary' onClick={this.friendWindowClose}>关闭</Button>
              </div>,
            ]}
          >
            <Table size='small' columns={columns}
              style={{ marginTop: '12px' }}
              dataSource={this.state.friendList}
              pagination={{ pageSize: 10 }}
              customRow="click"
              onRow={(record) => {
                return {
                  onClick: () => {
                    console.log(record)
                    this.setState({ friendInfoWindow: true })
                    this.setState({ friendInfo: record[0] })
                  }
                }
              }
              }
            />
            <Modal visible={this.state.friendInfoWindow}
              onCancel={this.friendInfoClose}
              footer={[
                <div style={{ textAlign: 'center' }} >

                </div>,
                <div style={{ textAlign: 'center' }} >
                  <CopyToClipboard text={this.state.friendInfo}>
                    <Button type='primary' onClick={this.friendInfoClose}>
                      关闭
                    </Button>
                  </CopyToClipboard>
                </div>,
              ]}
            >
              <div style={{ marginTop: '10px', width: '90%' }}>
                {this.state.friendInfo}
                <CopyToClipboard text={this.state.friendInfo}>
                  <Button><CopyOutlined /></Button>
                </CopyToClipboard>
              </div>
            </Modal>
          </Modal>
        </Card>
        <LGCwindow ref={LGCwindow => { this.LGCwindow = LGCwindow }} />

      </div>
    );
  }
}

export default lgairdrop;
